<template>
  <div class="centerBlock">
    <div id="top-heading">
      <h1>Monster Slayer</h1>
    </div>
    <div id="main-block">
      <div class="health">
        <h4>Monster Health</h4>
        <div class="health-bar-border">
          <div class="health-bar" :style="monsterBar"></div>
        </div>
      </div>
      <div class="health">
        <h4>Your Health</h4>
        <div class="health-bar-border">
          <div class="health-bar" :style="playerBar"></div>
        </div>
      </div>

      <div class="actions">
        <div v-if="isGameOver" id="gameOverBox">
          <h3>Game Over!</h3>
          <h4 v-if="winner === 'player'">You won :)</h4>
          <h4 v-if="winner === 'monster'">You lost :(</h4>
          <button @click="restart()">Restart</button>
        </div>
        <div v-if="!isGameOver">
          <div class="box">
            <div class="section">
              <button @click="attackMonster()">Attack</button>
            </div>
            <div class="section">
              <button
                :disabled="specialAttackCount > 0"
                @click="specialAttack()"
              >
                Special Attack
              </button>
            </div>
          </div>
          <div class="box">
            <div class="section">
              <button :disabled="!(healCount > 0)" @click="healPlayer()">
                Heal
              </button>
            </div>
            <div class="section">
              <button @click="surrender()">Surrender</button>
            </div>
          </div>
        </div>
      </div>
      <h4>{{ this.winner }}</h4>
      <!-- <h4>Player{{ this.playerValue }}</h4> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "MonsterGame",
  data() {
    return {
      monsterValue: 100,
      playerValue: 100,
      currentRound: null,
      healCount: 0,
      specialAttackCount: 0,
      isGameOver: false,
      winner: "",
    };
  },
  watch: {
      playerValue() {
        if (this.playerValue <= 0) {
            this.winner = "monster";
            this.isGameOver = true;
        }
      },
      monsterValue() {
          if(this.monsterValue <= 0) {
              this.winner = "player";
              this.isGameOver = true;
          }
      }
  },
  computed: {
    monsterBar() {
      return {
        width: this.monsterValue + "%",
      };
    },
    playerBar() {
      return {
        width: this.playerValue + "%",
      };
    },
  },
  methods: {
    attackValue(min, max) {
      const value = Math.round(Math.random() * (max - min) + min);
      return value;
    },
    attackMonster() {
      if (this.monsterValue <= 0) {
        this.isGameOver = true;
        return (this.monsterValue = 0);
      }
      this.currentRound++;
      if (this.currentRound > 0 && this.currentRound % 4 === 0) {
        this.currentRound = 0;
        this.healCount += 1;
      }
      this.monsterValue -= this.attackValue(5, 12);
      this.attackPlayer();
    },
    attackPlayer() {
      if (this.playerValue <= 0) {
        this.isGameOver = true;
        return (this.playerValue = 0);
      }
      this.playerValue -= this.attackValue(8, 15);
    },
    healPlayer() {
      this.currentRound++;
      this.healCount -= 1;
      if (this.playerValue >= 100) {
        return (this.playerValue = 100);
      }
      this.playerValue += this.attackValue(11, 15);
      this.attackPlayer();
    },
    specialAttack() {
      this.specialAttackCount = 1;
      this.monsterValue -= this.attackValue(12, 20);
    },
    surrender() {
        this.isGameOver = true;
    },
    restart() {
        this.currentRound = null;
        this.isGameOver = false;
        this.winner = "",
        this.healCount = 0;
        this.specialAttackCount = 0;
        this.monsterValue = 100;
        this.playerValue = 100;
    }
  },
};
</script>

<style scoped>
.centerBlock {
  margin: auto;
  margin-top: 0px !important;
  width: 40%;
}
#top-heading {
  padding: 20px;
  background-color: rgb(143, 30, 30);
  color: white;
}
.health {
  padding: 15px;
  border: 1px solid rgb(78, 74, 74);
  border-radius: 6px;
  margin: 4px;
  box-shadow: 3px 5px 10px #888888;
}
.health-bar-border {
  border: 1px solid black;
  background-color: azure;
  border-radius: 5px;
}
.health-bar {
  width: 100%;
  height: 27px;
  background-color: green;
  /* border: 1px solid black; */
  border-radius: 5px;
}
.actions {
  margin-top: 10px;
}
.box {
  display: flex;
}
.section {
  background-color: rgb(232, 243, 243);
  padding: 10px 15px;
  margin: 2px;
  height: 50px;
  width: 100%;
}
button {
  padding: 14px;
  background-color: rgb(59, 159, 226);
  border-radius: 3px;
  border: 1px solid rgb(59, 159, 226);
}
button:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
#gameOverBox {
    background-color: rgb(31, 29, 29);
    color:white;
    border-radius: 3px;
    margin: 2px;
    padding: 20px;
}
</style>